#framTwoPages {
  background-color: white;
  height: 100vh;
  position: relative;
}

#navbarFrameTwoPages {
  /* Position*/
  position: absolute;
  top: 1vh;

  /* Dimension & background*/
  width: 100%;


  /* Inline */
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-column-gap: 10px;
  

  /*READ MORE ABOUT*/
  /* https://css-tricks.com/flexbox-like-just-put-elements-in-a-row-with-css-grid/ */
}

#navbarBack {
  height: 10vh;

  display: flex;
  justify-content: left;

  margin-left: 10vh;
}

#navbarText {
  height: 10vh;
  align-items: center;

  font-size: 3vh;
  
  display: flex;
  justify-content: center;
}

#navbarInfo { 
  height: 10vh;

  display: flex;
  justify-content: flex-end;

  margin-right: 10vh;
}


#firstTopImages {
  background-color: #ececec;

  height: 0%;
  width: 100%;
}

#secondMask {
  /* background-color: indigo; */
  position: absolute;

  width: 100%;
  height: 100%;

  z-index: 10;
}

#secondPartMaskProtocolloDue {
  background-color: #ececec;

  height: 10%;
  width: 100%;
}

#firstPartMask {
  display: grid;
  grid-template-columns: 1% 48.5% 1% 48.5% 1%;

  height: 90%;
  width: 100%;
}

#thirdBottomImages {
  background-color: #ececec;

  height: 100%;
  width: 100%;
  z-index: 11;
}

#firstLeftBorder {
  background-color: #ececec;
  display: flex;
}



