@media only screen and (max-width: 800px) {
  #thirdCentralImage {
    height: 0;
    width: 0;
  }

  #fourthMaskImageReal {
    height: 0;
    width: 0;
  }

  #thirdCentral {
    height: 0;
    width: 0;
  }

  #fourthMaskImage {
    height: 0;
    width: 0;
  }

  #imageBackground {
    grid-template-columns: 3% 94% 0% 0% 3%;
  }

  #firstPartMask {
    grid-template-columns: 3% 94% 0% 0% 3%;
  }
}
